import React, { useState } from 'react';
import EmailIcon from '@mui/icons-material/Email';
import HomeWorkIcon from '@mui/icons-material/HomeWork';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PhoneIcon from '@mui/icons-material/Phone';
import { Box, Typography, TextField, Button, Grid } from '@mui/material';
import axios from 'axios';

function Contact() {
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    email: '',
    message: ''
  });

  const rootStyle = {
    padding: '0 8% 5% 8%',
  };

  const formContainerStyle = {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  };

  const textFieldStyle = {
    marginBottom: '16px',
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    axios.post('http://localhost:5000/send', formData)
      .then(response => {
        alert('Message sent successfully!');
      })
      .catch(error => {
        alert('Failed to send message.');
        console.error(error);
      });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  return (
    <Box style={rootStyle}>
      <p className='header-title-2'>THNG TIN LIN H</p>

      <Grid item xs={12} md={12} style={{ display: 'flex', height: '400px' }}>
        <Box
          component="iframe"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3918.6100105370224!2d106.8073080748579!3d10.84112758931161!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31752731176b07b1%3A0xb752b24b379bae5e!2sFPT%20University%20HCMC!5e0!3m2!1sen!2s!4v1717595932717!5m2!1sen!2s"
          style={{ border: 0, flexGrow: 1 }}
          allowFullScreen=""
          loading="lazy"
          referrerPolicy="no-referrer-when-downgrade"
        ></Box>
      </Grid>
      <Grid container spacing={2} style={{ height: '100%' }}>
        <Grid item xs={12} md={7} style={{ display: 'flex', alignItems: 'center' }}>
          <div style={formContainerStyle}>
            <Typography variant="h6" align="center" gutterBottom className='title-contact'>
              <Box display="flex" alignItems="center" justifyContent="left">
                <EmailIcon style={{ marginRight: 8 }} />
                 li li nhn cho chng ti
              </Box>
            </Typography>
            <form onSubmit={handleSubmit} style={{ width: '100%' }}>
              <Grid container spacing={2} item xs={12}>
                <Grid container spacing={2} item xs={6}>
                  <Grid item xs={12}>
                    <TextField
                      style={textFieldStyle}
                      label="H v tn ca bn"
                      variant="outlined"
                      fullWidth
                      required
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      style={textFieldStyle}
                      label="S in thoi ca bn"
                      variant="outlined"
                      fullWidth
                      required
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      style={textFieldStyle}
                      label="Email ca bn"
                      variant="outlined"
                      fullWidth
                      required
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                    />
                  </Grid>
                </Grid>

                <Grid item xs={6}>
                  <Grid item xs={12}>
                    <TextField
                      style={textFieldStyle}
                      label="Ni dung"
                      variant="outlined"
                      fullWidth
                      multiline
                      rows={6}
                      required
                      name="message"
                      value={formData.message}
                      onChange={handleChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      fullWidth
                    >
                      GI LIN H
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </form>
          </div>
        </Grid>
        <Grid item xs={12} md={5} >
          <div style={formContainerStyle}>
            <Typography variant="h6" align="center" gutterBottom className='title-contact'>
              <Box display="flex" alignItems="center" justifyContent="left">
                <HomeWorkIcon style={{ marginRight: 8 }} />
                Thng tin lin h
              </Box>
            </Typography>
            <Grid item xs={12} md={12} style={{}}>
              <Box display="flex" alignItems="center" justifyContent="left">
                <LocationOnIcon style={{ marginRight: 8 }} />
                <p><strong>a ch</strong><br />22 L Chiu Hong, Phng 10, Qun 6, TP HCM</p>
              </Box>
              <Box display="flex" alignItems="center" justifyContent="left">
                <MailOutlineIcon style={{ marginRight: 8 }} />
                <p><strong>Email</strong><br />Daithuanphat@gmail.com</p>
              </Box>
              <Box display="flex" alignItems="center" justifyContent="left">
                <PhoneIcon style={{ marginRight: 8 }} />
                <p><strong>in thoi</strong><br />+84 (24) 8888 9999</p>
              </Box>
            </Grid>
          </div>
        </Grid>
      </Grid>
    </Box>
  );
}

export default Contact;
